import {
  getList,
  getTags,
  getYourFeed,
  addfavorite,
  delfavorite,
} from '@/api/article'
import { mapState } from 'vuex'
export default {
  name: 'homeIndex',
  async asyncData({ query, store }) {
    const page = Number(query.page || 1)
    const limit = 20
    const tab = query.tab || 'global_feed'
    const { tag } = query
    const loadArt =
      store.state.user && tab == 'your_feed' ? getYourFeed : getList
    const [art, tagData] = await Promise.all([
      loadArt({
        limit: limit,
        offset: (page - 1) * limit,
        tag,
      }),
      getTags(),
    ])
    const { articles, articlesCount } = art.data
    articles.forEach((item) => {
      item.favoritDisable = false
    })
    const { tags } = tagData.data
    return {
      articlesCount,
      articles,
      limit,
      page,
      tags,
      tag,
      tab,
    }
  },
  watchQuery: ['page', 'tag', 'tab'],
  computed: {
    ...mapState(['user']),
    totalPage() {
      return Math.ceil(this.articlesCount / this.limit)
    },
  },
  methods: {
    async onfavorite(item) {
      item.favoritDisable = true
      if (item.favorited) {
        //取消点赞
        await delfavorite(item.slug)
        item.favorited = false
        item.favoritesCount += -1
      } else {
        //点赞
        await addfavorite(item.slug)
        item.favorited = true
        item.favoritesCount += 1
      }
      item.favoritDisable = false
    },
  },
}
